























































































































































































































.SignIn {
  width: 100vw;
  height: 100vh;
  font-size: 4vw;
  // padding: 7vh 0;
  // background: linear-gradient(116deg, #41bb64, #0a4fa0);
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  .topback {
    height: 40%;
    width: 100%;
    background: url("../../../assets/phone/AppVisitorHome/top_back.png")
      no-repeat;
    background-size: 100% 100%;
    position: absolute;
    top: 0;
  }
  .bottomTitle {
    font-size: 12px;
    color: #d1d1d1;
    position: absolute;
    bottom: -2vh;
    font-weight: 400;
    text-align: center;
  }
  .bottomback {
    height: 5%;
    width: 100%;
    background: url("../../../assets/phone/AppVisitorHome/bottom_back.png")
      no-repeat;
    background-size: 100% 100%;
    position: absolute;
    margin-top: 0;
    bottom: -10vh;
  }
  .SignInTitle {
    font-size: 8.5vw;
    font-weight: 800;
    text-align: center;
    padding-top: 4.47vh;
    box-sizing: border-box;
    margin: 0 auto;
    color: white;
    position: absolute;
    text-align: center;
    top: 0;
    width: 100%;
  }
  .SignInContent {
    width: 88%;
    // height: 82vh;
    background-color: white;
    border-radius: 2vw;
    position: absolute;
    top: 20.29vh;
    padding-bottom: 2vh;
    border: 0.05vw solid #00000018;
    box-shadow: 0vh 0vw 5vw 3vw #00000018;
    // overflow-y: auto;
    // &::-webkit-scrollbar {
    //   display: none;
    // }
    .TopImg {
      width: 20vw;
      height: 20vw;
      margin-top: 0 !important;
      position: absolute;
      top: -10vw;
      left: calc(50% - 10vw);
      background: url("../../../assets/phone/AppVisitorHome/icon_apphome.png")
        no-repeat;
      background-size: 100% 100%;
      img {
        width: 20vw;
      }
    }
    .SignInContentBox {
      height: 100%;
      margin-top: 9.48vh;
      list-style: none;
      text-align: center;

      li {
        margin-top: 5vw;
        position: relative;
        input {
          border: 0.4vw solid rgb(153, 154, 155);
          background-color: #c7c5c521;
          border-radius: 5vw;
          width: 62.07vw;
          padding: 1vh 4vw 1vh 8vw;
          // text-indent: 2vw;
        }
        .codeInput {
          width: 46.07vw;
          padding: 1vh 20vw 1vh 8vw;
        }
        .countDown60 {
          position: absolute;
          top: 2vw;
          right: 8vw;
          font-size: 3vw;
        }
      }
      .SignInBtn {
        width: 75vw;
        height: 6vh;
        background: linear-gradient(110deg, #41bb64, #0a4fa0);
        color: white;
        text-align: center;
        border-radius: 7vw;
        font-size: 5vw;
        font-weight: 400;
        line-height: 6vh;
        margin: 6vw auto 0;
      }
      .guarantee {
        font-size: 3.2vw;
        margin: 3vw 0 0 4vw;
        line-height: 3.4w;
        color: #cacaca;
        display: flex;
        justify-content: center;
        align-items: center;
        input[type="checkbox"] {
          width: 3.06vw;
          height: 1.63vh;
          margin-right: 4vw;
          position: relative;
          // &:after {
          //   position: absolute;
          //   width: 100%;
          //   height: 100%;
          //   content: " ";
          //   background-color: #d8d8d8;
          //   visibility: visible;
          //   border: 1px solid #979797;
          // }
          // &:checked:after {
          //   content: "✔";
          //   font-size: 3.2vw;
          //   width: 100%;
          //   height: 100%;
          //   position: absolute;
          //   color: #fff;
          //   background-color: #2acfec;
          //   padding-bottom: 0.4vh;
          //   padding-left: 0.3vw;
          //   left: 0;
          //   top: 0;
          // }
        }

        span {
          margin-top: 0.3vh;
          .notice {
            color: #0379b0;
          }
        }
      }
      .have {
        font-size: 3vw;
        span {
          color: #146296;
        }
      }

      .registerInput {
        position: relative;
        img {
          position: absolute;
          top: 1.5vw;
          left: 7.2vw;
          width: 5.5vw;
        }
        // input {
        //   width: 64.07vw;
        //   padding: 1vh 6vw;
        //   text-indent: 1vw;
        // }
      }
    }
  }
}
