





































































































































.NumberLogin {
  width: 100vw;
  height: 100vh;
  font-size: 4vw;
  // background: linear-gradient(116deg, #41bb64, #0a4fa0);
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  .topback {
    height: 40%;
    width: 100%;
    background: url("../../../assets/phone/AppVisitorHome/top_back.png")
      no-repeat;
    background-size: 100% 100%;
    position: absolute;
    top: 0;
  }
  .bottomTitle {
    font-size: 12px;
    color: #d1d1d1;
    position: absolute;
    bottom: 10vh;
    font-weight: 400;
    text-align: center;
  }
  .bottomback {
    height: 5%;
    width: 100%;
    background: url("../../../assets/phone/AppVisitorHome/bottom_back.png")
      no-repeat;
    background-size: 100% 100%;
    position: absolute;
    bottom: 0;
  }
  .NumberLoginTitle {
    font-size: 8.5vw;
    font-weight: 800;
    text-align: center;
    padding-top: 4.47vh;
    box-sizing: border-box;
    margin: 0 auto;
    color: white;
    position: absolute;
    text-align: center;
    top: 0;
    width: 100%;
  }
  .NumberLoginContent {
    width: 85vw;
    // height: 78vh;
    background-color: white;
    border-radius: 2vw;
    position: absolute;
    top: 20.29vh;
    padding-bottom: 5vh;
    border: 0.05vw solid #00000018;
    box-shadow: 0vh 0vw 5vw 3vw #00000018;
    .TopImg {
      width: 20vw;
      height: 20vw;
      margin-top: 0 !important;
      position: absolute;
      top: -10vw;
      left: calc(50% - 10vw);
      background: url("../../../assets/phone/AppVisitorHome/icon_apphome.png")
        no-repeat;
      background-size: 100% 100%;
      img {
        width: 20vw;
      }
    }
    .NumberLoginContentBox {
      list-style: none;
      text-align: center;
      margin-top: 9.48vh;
      li {
        margin-top: 5vw;
        position: relative;
        input {
          width: 70vw;
          border: 0.4vw solid rgb(153, 154, 155);
          background-color: #c7c5c521;
          border-radius: 4vw;
          padding: 2vw 0 2vw 4vw;
        }
      }
      .NumberLoginBtn {
        width: 75vw;
        height: 6vh;
        background: linear-gradient(110deg, #41bb64, #0a4fa0);
        color: white;
        text-align: center;
        border-radius: 7vw;
        font-size: 5vw;
        font-weight: 400;
        line-height: 6vh;
        margin: 6vw auto 0;
      }
      .LoginProblem {
        width: 85%;
        display: flex;
        justify-content: space-between;
        margin: 6vw auto 0;
        color: #146296;
      }
      .TopImg {
        padding-top: 10vw;
        padding-bottom: 3vw;
        img {
          width: 20vw;
        }
      }
    }
  }
}
